.content-appeal {
  max-width: 960px;
  height: calc(100vh - 50px);

  margin: 50px auto;
  margin-bottom: 0;

  padding: 20px 0;
}

.content-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  height: 100%;

  padding-bottom: 1rem;

  background-color: #FFF;
  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  border-radius: 5px;
}



/* ======= Top-block =======*/
.content-block {
  padding: 0 1rem;
}

.content-top-block {
  width: 100%;
  height: 15%;
}

.content-title {
  margin: 0;
  line-height: 2rem;

  border-bottom: 2px solid rgb(184, 184, 184);
}



.table-appeal tbody tr td{
  width: 100%;
  
  padding: 2px 4px;
  padding-top: 6px;

  vertical-align: top;

  &:nth-child(2) {
    width: 240px;
  }

  &:last-child {
    position: relative;

    width: 160px;
    text-align: left;
  }

  & .date-term {
    position: absolute;
    top: 8px;
    right: 15px;

    font-size: 24px;
    font-weight: bold;
  }
}

/* ======= Left-block =======*/
.content-left-block {
  width: 30%;
  height: 85%;
  
  border-right: 2px solid #dbd8d8;

  overflow-y: auto;
}

.services-list {
  height: 100%;

  margin: 0 10px;
  padding: 0;
  
  list-style: none;

  font-size: 16px;
  font-weight: bold;
 
}

.services-list-itme {
  display: flex;
  flex-direction: column;

  margin-bottom: 15px;
  padding: 1rem;

  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  border-radius: 5px;
  background-color: #f3f7fa;

  & h3 {
    margin: 0;
  }

  &:hover {
    background-color: #6658dd;

    cursor: pointer;
    color: #FFF;
  }

  &:active {
    background-color: #69c2f5;
    box-shadow: 0px 0px 10px 2px rgba(22, 22, 223, 0.35);

    cursor: pointer;
    color: #FFF;
  }
}

/* ======= Right-block =======*/
.content-right-block{
  width: 70%;
  height: 85%;
}

.right-block-head {
  display: flex;
  flex-direction: column;
  
  height: 10%;

  border-bottom: 2px solid #dbd8d8;

  & .btn-next-stage {
    padding: .3rem;
    font-size: 12px;

    background-color: #6658dd;

    &:hover {
      background-color: #4c1fee;
      border-color: #4c1fee;
    }

    &:active {
      background-color: #db2c0d;
      box-shadow: 0 0 3px 1px rgb(204, 31, 0);
    }
  }

  & h2 {
    margin: 0;
  }

  & .flex {
    display: flex;
    justify-content: space-between;

    margin-bottom: 15px;
    
  }

  & span {
    font-size: 16px;
    color: #b3b3b6;
  }
}

.right-block-nav {
  height: 16%;

  margin-top: 2%;
}

.nav-tabs-svg {
  display: flex;
  justify-content: space-between;

  margin: 0;
  padding: 0;

  font-size: 18px;
  font-weight: bold;
  color: #7c788a;
  text-align: center;
  list-style: none;

  & .tab-item-svg {
    position: relative;
    width: 120px;

    padding: 10px 0;
    user-select: none;

    &:not(:last-child)::after {
      content: "";

      position: absolute;
      top: 10px;
      bottom: 10px;
      right: -30px;

      width: 4px;

      background-color: #dbd8d8;
      border-radius: 5px;
    }
    

    &:hover {
      border-radius: 6px;
      background-color: rgb(165, 199, 250);
      box-shadow: 0 0 3px 2px rgb(143, 184, 245);
  
      color: #fff;  
    }

    &:active {
      border-radius: 6px;
      background-color: rgb(141, 247, 138);
      box-shadow: 0 0 3px 2px rgb(106, 255, 101);
  
      color: black;  
    }

  }
}


.right-block-body {
  height: 72%;
}

.tab-content-block {
  display: none;

  height: 100%;

  overflow: hidden;
  overflow-y: auto;
}

.form-note {
  position: relative;
  
  padding-top: 5px;
}

.blcok-comments {
  display: flex;
  flex-direction: column;
  

  & .comment {
    max-width: 300px;
    margin-bottom: 15px;
    padding: 10px 5px;

    border-radius: 8px;
    border: 3px solid #685bdb;
    

    & time {
      font-size: 12px;
      font-weight: bold;
      color: #7e7e7e;
    }

    & p {
      margin: 0;

      font-size: 14px;
      color: #000000;
    }
  }

  & .right-comment {
    margin-left: auto;
    margin-right: 10px;
    border-right: transparent;
    box-shadow: -5px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  }

  & .left-comment {
    margin-right: auto;
    margin-left: 10px;
    border-left: transparent;
    box-shadow: 5px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  }
}

.blcok-enter-comment{
  display: flex;

  
  position: sticky;

  bottom: 0;
  margin: 0;
  padding: 10px 10px; 

  border-radius: 5px;
  background-color: #f1f1ea;
  box-shadow: 1px 3px 1px #faf5f5;
  border-radius: 10px;
}

.field-enter-comment {
  display: block;

  width: 95%;
  padding: 10px 10px;

  font-size: 16px;
  line-height: 24px;

  outline: #ffea2a;
  border: none;
  border-radius: 20px;
  background-color: #fff;
}

.btn-enter-comment {
  width: 45px;
  height: 35px;

  background-color: transparent;
  & svg {fill: #0664f1;} 
}

.active {
  display: block;
}

.check {
  border-bottom: 4px solid rgb(50, 32, 128);
  border-radius: 4px;
}

.block-documents {
  display: flex;
  justify-content: space-between;

  & h2 {
   
    margin: 0;
  }

  & p {
    margin: 0;
    margin-left: 30px;
    padding-top: 5px; 

    color: rgb(255, 17, 17);

    & svg {
      vertical-align: bottom;
      fill: rgb(0, 119, 255);
    }
  }
 
}


/* ======= Accordion =======*/
.container-documents {
  width: 90%;
  margin: 50px auto;
}

button.accordion {
  width: 100%;
  background-color: whitesmoke;
  border: none;
  outline: none;
  text-align: left;
  padding: 15px 20px;
  font-size: 18px;
  color: #333;
  cursor: pointer;
  transition: background-color 0.2s linear;
}

button.accordion:after {
  content: '\f055';
  font-family: "fontawesome";
  font-size: 14px;
  float: right;
	
}

button.accordion.is-open:after {
  content: '\f056';
}

button.accordion:hover, button.accordion.is-open {
  background-color: #ddd;
}

.accordion-content {
  background-color: white;
  border-left: 1px solid whitesmoke;
  border-right: 1px solid whitesmoke;
  padding: 0 20px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;
}
/* ======= Accordion =======*/

.reference-list {
  padding: 0;
  padding-left: 10px;
  
  font-size: 16px;
  font-weight: bold;
}

.flex-wrapper {
  display: flex;
  justify-content: space-between;

  & svg {
    vertical-align: bottom;
  }
}