*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 14px;
}

 body {  
    margin: 0;
    font-family: Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    font-size: .875rem;
    font-weight: 400;
    line-height: 1.5;
    color: #000000;
    text-align: left;
    background-color: #f5f9fc;
}

a {
  text-decoration: none;
  color: #fff;
  
  &:hover {
    color: #fff;
    cursor: pointer;
  }
}

button {
  padding: .4rem .8rem;
 
  color: #fff;
  font-weight: bold;
  text-align: center;
   
  user-select: none;
  font-size: 1.1rem;
  line-height: 1.5rem;
   
  border: none;
  outline: none;
  border-radius: .25rem;
  
  cursor: pointer;
}

.wrrapper {
  display: flex;
 
  height: 100vh;
  overflow: hidden;
  width: 100%;
}

/* ======= Section - navbar =======*/
.navbar-custom {
  display: flex;

  background-color: #38414a;
  box-shadow: 0 0 25px 0 rgba(154,161,171,.15);

  padding: 0.5rem 0;
  padding-left: 3rem;

  position: fixed;
  left: 0;
  right: 0;
  height: 50px;
  z-index: 100;

}

  .logo img{
    width: 180px; 
    height: 100%;
  }

  .button-new-appeal {
    margin-left: 4rem;

    border: 1px solid #4631ff;
    background-color: #4631ff;
   
    &:hover {
      background-color: #4c1fee;
      border-color: #4c1fee;
    
      transition-duration: 0.2s;
    }

    & span{
      font-weight: normal;

      margin-left: 0.4rem;
      vertical-align: middle;
      font-size: 2.4rem;
    }

    &:active {
      background-color: #8275ee;
      box-shadow: 0 0 2px 2px rgb(71, 51, 250);
      transition-duration: 0.2s;
    }

  }



/* ======= Main =======*/
.page-main {
  width: 100%;
  height: 100vh;

  background-color: #f3f7fa;
}


/* ======= Filter-nav =======*/
.left-filter-menu {
  position: fixed;

  width: 260px;
  height: calc(100%-50px);

  margin-top: 50px;
  padding: 10px 10px;

  overflow-y: hidden;
  background-color: #FFF;
  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
}

.filter-list {
  padding: 0;
  list-style: none;

  font-size: 16px;
}

.filter-list-item {
  margin-top: 20px; 

  & label {
    font-weight: bold;
  }
}

.select2 {
  width: 100%;
  max-width: 240px;
}

.btn-wrapper {
  display: flex;

  margin-top: 30px;
}

.btn-filter {
  position: relative;
  padding: 0.5rem 0.8rem;

  font-size: 1rem;
}

.button-decline {
  margin-right: auto;

  background-color: #a50404d4;
  
  &:active {
    background-color: #f34646d4;
    box-shadow: 0 0 2px 2px rgb(243, 27, 27);
  } 
}

.button-apply {
  background-color: #0c740c;

  &:active {
    background-color: #32b632;
    box-shadow: 0 0 2px 2px rgb(13, 151, 54);
  } 
}
/* ======= Content-page =======*/
.content-page {
  height: calc(100vh-50px);

  margin-left: 260px;
  margin-top: 50px;
  padding: 20px;

  overflow: hidden;
}

.content {
  height: 100%;

  padding-bottom: 3rem;
  background-color: #FFF;
  box-shadow: 0px 1px 3px rgba(63, 63, 68, 0.15), 1px -1px 4px rgba(63, 63, 68, 0.04);
  border-radius: 3px;
}

.data-table {
  display: block;
  width: 100%;
  height: 100%;

  padding: 1rem;

  &-body {
    height: calc(100% - 134px);

    overflow-x: auto;
  }
}


.table-head thead tr th {
    position: sticky;
    top: 0;
    padding-top: 0;
    text-align: left;
}


.table {
  position: relative;
  
  width: 100%;

  border-collapse: collapse;
  table-layout: fixed;

  font-size: 14px;
  vertical-align: middle;
  text-align: center;
  overflow: hidden;
  

  & thead tr th {

    padding: 18px 10px;
    border-bottom: 1px solid #848484;

    font-size: 18px;

    & > * {
      display: block;
    }

    &:last-child {
      position: relative;

      padding-right: 30px;
    }

    
  }

  & tbody tr td{
    padding: 8px 10px;
    border-bottom: 1px solid #848484;
    text-align: left;

    & .date {
      font-size: 12px;
    }

    & .phone-number {
      font-size: 12px;  
    }

    & .item-ref {
      color: #343a40;

      text-decoration: underline;

      &:hover {
        color: #00bdec;
      }

      &:active {
        color: #c70000;
      }
    }
    
    & > * {
      display: block;
    }

  }
}

.wrapper-search {
  display: flex;
  position: relative;

  padding: 5px 10px;
  background-color: #4a4a4a;
  border-radius: 10px;

  & svg {
    position: relative;
    
    margin-left: 5px;
    vertical-align: middle;
    cursor: pointer;
    fill: #0086ff;

    z-index: 10;
  }

  &::after {
    content: '';

    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 45px;

    border-radius: 0px 10px 10px 0px;
    background-color:#4a4a4a;

    z-index: 1;
  }
}

.input-search {
  width: calc(100% - 40px);

  padding: 4px 2px;
  padding-left: 8px;

  border: none;
  border-radius: 10px 0px 0px 10px;

  font-size: 1rem;
  line-height: 2rem;

  box-shadow:inset 0px 0px 5px 1px rgb(57, 159, 206);
}

.cell-relat {
  position: relative;

  padding-right: 20px !important;

  & .days {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 38px;
  }
}

.toggler-wrapper {
  position: absolute;

  top: 0;
  right: 0;

  width: 20px;


  & .toggler {
   
    width: 1px;
    height: 1px;

    padding: 0;
    font-size: 0;
    line-height: 0;

    border: 10px solid transparent; 
    background-color: transparent;
  }

  & .top-tgl {
    border-bottom: 15px solid #343a40; 
  }
  & .bot-tgl {
    border-top: 15px solid #343a40; 
  }
}


.blu {
  color: #1c69b7 !important;
  font-weight: bold;
}

.grn {
  color: green !important;
  font-weight: bold;
}

.rd {
  color: rgb(223, 17, 17) !important;
  font-weight: bold;
}

.bg-blu {
  width: 120px;

  border-radius: 10px;
  background-color: #195a9c !important;

  color: #fff;
  text-align: center;
  font-weight: bold;
}

.bg-grn {
  width: 120px;
  
  border-radius: 10px;
  background-color: #1e781e !important;

  color: #fff;
  text-align: center;
  font-weight: bold;
}

.bg-rd {
  width: 120px;
  
  border-radius: 10px;
  background-color:  #a11111 !important;

  color: #fff;
  text-align: center;
  font-weight: bold;
}

/* ======= Colgroup =======*/
.col {
  width: 100%;
}

.number-col {
  width: 100px;
}

.stage-col {
  width: 130px;
}
/* ======= Colgroup =======*/